const getPresentDatesSet = (presentDates) => {
    const presentSet = new Set();
    presentDates.forEach((item) => {
        let date = new Date(item);
        date.setDate(date.getDate() - 1);
       
        const prevIsoDate = date.toISOString();
        presentSet.add(prevIsoDate.split("T")[0]);
      });
    return presentSet ;
}

const getAbsentDatesSet = (absentDates) => {
    const absentSet = new Set();
    absentDates.forEach((item) => {
        let date = new Date(item);
        date.setDate(date.getDate() - 1);
       
        const prevIsoDate = date.toISOString();
        absentSet.add(prevIsoDate.split("T")[0]);
      });
    return absentSet ;
}

const getTotalDatesSet = (totalDates) => {
    const totalSet = new Set();
    totalDates.forEach((item) => {
        let date = new Date(item);
        date.setDate(date.getDate() - 1);
      
        const prevIsoDate = date.toISOString();
        totalSet.add(prevIsoDate.split("T")[0]);
      });
    return totalSet;
}

module.exports = {getAbsentDatesSet,getPresentDatesSet,getTotalDatesSet}