import Spinner from "react-spinkit";
import { useForm } from "react-hook-form";
import { useState, useRef } from "react";
import { singup } from "./helper/AuthApiCalls";
import toast, { Toaster } from "react-hot-toast";

import { Redirect } from "react-router-dom"; 
const successSingup = () => {
  toast.success("Singup Success", {
    duration: 1000,
  });
};

function Signup({history}) {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const [isLoading, setisLoading] = useState(false);
  const [error, seterror] = useState(false);
  const [redirect, setRedirect] = useState(false);
  const buttonRef = useRef(null);
  function sleep(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }
  
  const onSubmit = (data) => {
    buttonRef.current.classList.add("hidden");

    setisLoading(true);
    // call api to log in
    singup(data)
      .then(async (user) => {
        successSingup();
        await sleep(500); 
        setRedirect(true) ;
      })
      .catch(async (Error) => {
        await sleep(500);
        buttonRef.current.classList.remove("hidden");
        setisLoading(false);
        seterror(true);
      });
  };

  const performRedirect = () => {
    if (redirect) return <Redirect to="/" />;
  };

  const singupForm = () => {
    return (
      <div className="grid h-screen place-items-center">
        <div className='w-full h-full flex flex-col justify-center items-center '>
        <form
          onSubmit={handleSubmit(onSubmit)}
          className="flex bg-white flex-col mt-44 m-4 md:w-2/5 md:ml-96 p-5 space-y-3 rounded-md shadow-2xl"
        >
          <h1 className="self-center text-black text-2xl font-semibold mb-2">
            Singup 
          </h1>
          <p className='text-red-300 '>Note! Singup feature is only for those who want to have admin featues </p>
          {error && (
            <h1 className="text-red-600 bg-red-200 p-2 ">
              Incorrect Email or Password
            </h1>
          )}
          <input
            {...register("email", { required: true })}
            type="email"
            name="email"
            autoComplete="off"
            placeholder="Email"
            className="p-3 text-black bg-[#EEEEEE] focus:outline-none"
          />
          {errors.email && (
            <span className="text-red-600 bg-red-200 p-2 w-4/5 md:w-1/2">
              Email is required
            </span>
          )}
          <input
            {...register("password", { required: true })}
            type="password"
            name="password"
            autoComplete="off"
            placeholder="Password"
            className="p-3 text-black bg-[#EEEEEE] focus:outline-none"
          />
          {errors.password && (
            <span className="text-red-600 bg-red-200 p-2 w-4/5 md:w-1/2">
              Password is required
            </span>
          )}
          <button
            ref={buttonRef}
            className="p-3 bg-[#32BD69] focus:outline-none text-white"
          >
            Signup
          </button>
          {isLoading && (
            <Spinner
              fadeIn="none"
              name="cube-grid"
              color="orange"
              className="self-center h-3"
            />
          )}
        </form>
        <p className='text-white font-bold text-xl'>Have an account already?</p>
        <button className='w-20 bg-indigo-500 rounded-2xl font-bold p-1'
         onClick = {()=>{history.push('/')}}
        >Singin</button>
        </div>
      </div>
    );
  };
  return (
    <>
     <Toaster />
      {singupForm()}
      {performRedirect()}
    </>
  );
}

export default Signup;
