import Modal from "react-modal";
import React, { useState } from "react";
import {
  getAbsentDatesSet,
  getPresentDatesSet,
  getTotalDatesSet,
} from "../../utitlity/Utitlity"; //utility functions set required dates
import DescriptiveModal from "./DescriptiveModal";

function UserInfoModal({ state, setOpenModal }) {
  const [isOpen, setIsOpen] = useState(true);
  const [presentDates, setPresent] = useState(new Set());
  const [absentDates, setAbsent] = useState(new Set());
  const [totalDates, setTotal] = useState(new Set());
  const [course, setCourse] = useState("");
  const [descriptiveModal, setDescriptiveModal] = useState(false);
  function closeModal() {
    setOpenModal(false);
    setIsOpen(false);
  }
  const handleAttendance = (course) => {
    const attendanceInfo = state.enrolled_course_attendance.find(
      (element) => element.course_name === course.course_name
    );
    setPresent(getPresentDatesSet(attendanceInfo.present_days));
    setAbsent(getAbsentDatesSet(attendanceInfo.absent_days));
    setTotal(getTotalDatesSet(course.dates));
    setCourse(course);
    setDescriptiveModal(true);
  };

  const consolidatedView = () => {
    if (
      presentDates &&
      absentDates &&
      totalDates &&
      course &&
      descriptiveModal
    ) {
      return (
        <DescriptiveModal
          presentDates={presentDates}
          absentDates={absentDates}
          totalDates={totalDates}
          course={course}
          setOpen={setDescriptiveModal}
        />
      );
    }
    return <></>;
  };

  return (
    <div>
      <Modal
        isOpen={isOpen}
        onRequestClose={closeModal}
        className=' bg-white w-[80%] rounded-md top-44 ml-6 md:m-0 absolute md:flex md:top-20 md:left-[36%] md:w-[400px] '
        contentLabel="Example Modal"
        style= {customStyles}
        ariaHideApp={false}
      >
        {consolidatedView()}

        <div className="text-center w-full ">
          <p className='text-xl font-bold text-[#06202A]'>{state.name}</p>
          <p className='text-lg font-semibold text-[#ccf1ff]'>
            Email:<span> {state.email} </span>
          </p>
          <p className='text-lg font-semibold text-[#06202A]'>
            Courses:
          </p>
            <div className='max-h-[300px] overflow-y-scroll p-2 m-2 text-lg  bg-purple-50'>
              {state.enrolled_course.length > 0 &&
                state.enrolled_course.map((course, index) => (
                  <div
                    key={course._id}
                    className="flex flex-col bg-purple-200   items-center p-2 mb-2"
                  >
                    <p className="text-green-800 font-bold text-xl">
                      # {index + 1}
                    </p>
                    <div className="flex">
                      {" "}
                      <p className="bg-purple-200 text-blue-500">
                        Course Name:
                      </p>
                      <span className="text-gray-500">
                        &nbsp;{course.course_name}
                      </span>
                    </div>
                    <div className="flex">
                      {" "}
                      <p className="bg-purple-200 text-blue-500">
                        Course Duration:
                      </p>
                      <span className="text-gray-500">
                        &nbsp;{course.course_duration} lectures
                      </span>
                    </div>
                    <div className="flex">
                      {" "}
                      <p className="bg-purple-200 text-blue-500">
                        Lectures Made:
                      </p>
                      <span className="text-gray-500">
                        &nbsp;{course.dates.length} lectures
                      </span>
                    </div>
                    <div className="flex">
                      {" "}
                      <p className="bg-purple-200 text-blue-500">Attended:</p>
                      {state.enrolled_course_attendance.map((item) => {
                        if (item.course_name === course.course_name)
                          return (
                            <span key={item._id} className="text-gray-500">
                              &nbsp;{item.present_days.length} lectures
                            </span>
                          );
                      })}
                    </div>
                    <div className="flex ">
                      <p className="bg-purple-200 text-blue-500">Percentage:</p>
                      {state.enrolled_course_attendance.map((item) => {
                        if (item.course_name === course.course_name)
                          return (
                            <span className="text-gray-500" key={item._id}>
                              &nbsp;
                              {Math.round(
                                ((item.present_days.length /
                                  (course.dates.length === 0
                                    ? 1
                                    : course.dates.length)) *
                                  100 +
                                  Number.EPSILON) *
                                  100
                              ) / 100}{" "}
                              % lectures
                            </span>
                          );
                      })}
                    </div>
                    {/* button  */}
                    <span
                      className=" group relative -space-y-1"
                      onClick={() => handleAttendance(course)}
                    >
                      <button className="btn btn-primary m-2">
                        <svg
                          className="w-6 h-6"
                          fill="currentColor"
                          viewBox="0 0 20 20"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            fillRule="evenodd"
                            d="M6 2a1 1 0 00-1 1v1H4a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V6a2 2 0 00-2-2h-1V3a1 1 0 10-2 0v1H7V3a1 1 0 00-1-1zm0 5a1 1 0 000 2h8a1 1 0 100-2H6z"
                            clipRule="evenodd"
                          ></path>
                        </svg>
                      </button> 
                      <p className='hidden absolute -right-7 -top-16  bg-blue-600 p-2 rounded-md text-white group-hover:inline-block '>
                        Consolidated View
                      </p>
                    </span>
                  </div>
                ))}
            </div>

        </div>
      </Modal>
    </div>
  );
}

const customStyles = {
  overlay: {
    position: "fixed",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: "rgba(0, 0, 0, 0.7)",
  },
 
};

export default UserInfoModal;
